<template> 
    <div class="content">

        <div >      
            <div class="manager">
             <div class="m-top">
                 <div class="m-lable"><div class="m-text">:</div><el-input style="width:10rem;" placeholder="" v-model="searchlist.projApprovalName"></el-input></div> 
                 <div class="m-lable"><div class="m-text">:</div><el-input style="width:10rem;" placeholder="" v-model="searchlist.projApprovalCode"></el-input></div> 
                 <div class="m-lable"><div class="m-text">:</div><el-input style="width:10rem;" placeholder=""></el-input></div> 
                 <el-button  style="width:8rem;background: rgb(221, 221, 221);" round ><i class="el-icon-search" style="font-size:1rem;margin-right:10px"></i></el-button>
                 <el-button   style="width:8rem;background: rgb(221, 221, 221);" round ><i class="el-icon-refresh" style="font-size:1rem;margin-right:10px"></i></el-button>
                 <div class="" style="line-height: 2.4rem;margin-left: 1rem;"><i class="el-icon-caret-bottom" style="margin-left: 0.5rem;" @click="unfold()"></i></div>
                <span style="line-height:2rem;color:red;">,.</span>
            </div> 
            <div style="display:flex;justify-content: space-between;margin-top:30px;margin-left:1rem"><el-button type="info" @click="adddialog=true"></el-button>
                <i class="el-icon-s-tools" slot="reference" style="font-size:2rem"  v-popover:popover>
                    <el-popover placement="top" offset="600"  width="300" trigger="click" ref="popover">
                        <div class="p-title" style="border-bottom:1px solid gray"> 
                            <!-- <el-checkbox label="" @click="quanxuan()" style="float:right"></el-checkbox>  -->
                        </div>
                        <el-checkbox-group 
                        v-model="checkedlists"
                        :min="1"
                        :change="itemschangehandler()"
                        style="display:flex;flex-flow: wrap;justify-content: space-between;"
                        >
                        <el-checkbox v-for="city in itemchecklist" v-model="city.show" :label="city.lable" :key="city.prop" style="width:40%" >{{city.lable}}</el-checkbox>
                    </el-checkbox-group>
                    </el-popover>
                </i>
            </div>
            <div class="table-box">
                <div class="table-left" style="width:90%">
                    <el-table
                    ref="multipleTable"
                    :data="tableData.data.result.records"
                    tooltip-effect="dark"
                    style="width: 100%;"
                    @selection-change="handleSelectionChange">
        
                    <el-table-column v-if="checkedlists.length!=0"
                    type="selection"
                    >
                    </el-table-column>
    
                    <el-table-column v-if="checkedlists.length!=0"
                    
                    label=""
                    fixed="right"
                    min-width="100"
                   >
                    <template slot-scope="scope">
                        <el-button
                        @click.native.prevent="deleteRow(scope.$index, tableData.data.result.records)"
                        type="text"
                        
                        size="small">
                        
                        </el-button>
                    </template>
                    </el-table-column>   
                    <div  v-for="(item) in itemchecklist"   :key="item.prop" >
                    <el-table-column  
                    min-width="200"
                    v-if="item.show==true"
                        :prop="item.prop"  
                        :label="item.lable"
                        :fixed="item.show==true&&(item.lable=='')"
                        >
                    </el-table-column>
       
                    </div>
                </el-table>
                <div class="pagination"><el-pagination style="float:right;margin-top:2rem;"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="searchlist.currentPage"
                    :page-sizes="[1, 2, 3]"
                    :page-size=searchlist.pagesize
                    layout="sizes, prev, pager, next"
                    :total=searchlist.total>
                        </el-pagination>
                        </div>
                </div>
                <div class="baseinfo">
                    <div class=""></div>
                    <div class="info"></div>
                    <i class="el-icon-caret-left" style="position: absolute;top:50%;left:10%;font-size: 2rem;" @click="showinfo()"></i>
                </div>
            </div>
        </div>
        </div>
    </div>
</template>
<style scoped>
    .dialog-title {
        margin-left: 1rem;
        font-size:1.4rem;
        font-weight: 700;
        text-align: start;
        border-bottom:1px solid black
    }
    .baseinfo{
        width:10%;
        border-left: 1px solid black;
        display: flex;
        flex-direction: column;
        position: relative;
            
    }
    .table-box{
        margin-top:1rem;
        border:1px solid black;
        display: flex;
        flex-direction: row;
        min-height: 500px;
    

    }
    .content{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction:column;
    }
    .manager{
        margin-left: 1%;
        border:1px solid black;
        margin-top: 1%;
        width: 94%;
        background: white;
        padding:1rem;
        box-sizing: border-box;
        flex-grow: 1;
    }
    .m-lable{
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-right:1rem;
       
    }
    .m-text{
        margin-right:1rem;
    }
    .m-top{
        display: flex;
        flex-direction: row;
    }
    .breadcrumb-box{
        display: flex;
        flex-direction:row;
        justify-content: start;
        box-sizing: border-box;
        padding:1rem;
        border:1px solid black;
        border-top:0px;
        border-radius:0px 0px 4px  4px ;
        background: white;
    }
    .bread{
        margin-left:1rem;
    }
    .breadcrumbtext
    {
        margin-left:0.2rem;
        margin-right:0.2rem;
    }
    .router-link-active{
        text-decoration: none;
        color: black;
    }
</style>
<script>

import axios from "axios";
export default {
    data(){
        return{
            //
            tableData:{
                data:{
                    result:{
                        records:[
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""},
                        {projApprovalName:"",creationDate:"2021-12-30",projectGroupId:"1123",swWaterStandard:"1",projectDeptName:"",projApprovalManagerName:"",createdByName:"",swExplain:""}
                        ]
                    }
                }
            },
            majorSrtruc:true,
          //
          multipleSelection:[],
        //
          checkedlists: ["","","","","","","",""],
            //
          itemchecklist:  [{lable:"",prop:"projApprovalName",show:true},{lable:"",prop:"creationDate",show:true},{lable:"",prop:"projectGroupId",show:true},{lable:"",prop:"swWaterStandard",show:true},{lable:"",prop:"projectDeptName",show:true},{lable:"",prop:"projApprovalManagerName",show:true},{lable:"",prop:"createdByName",show:true},{lable:"",prop:"swExplain",show:true},],
          //
          //
      
          adddialog:false,
          searchlist:{
            currentPage:1,
            pagesize:3,
            projApprovalCode:"",
            projApprovalName:"",
            identifier: "",
            inforStatus: "",
            isNotInforStatus: "",
            projApprovalManagerId: "",
            projRelationId: "",
            projectCategoryCode: "",
            projectGroupCode: "",
            projectGroupId: "",
            total:0,
          },
       
        }
    },methods:{
        //table
        toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },deleteRow(){},
      //
       getheader(){
         
       },
       //
       showinfo(){
        
       },
       //
       //  
       handleSizeChange(val){
        this.searchlist.pagesize=val;
        
       },
       //    
       handleCurrentChange(val){
        this.searchlist.currentPage=val;
     
       },
       //
       resetinput(){
            this.searchlist.projApprovalName=""
            this.searchlist.projApprovalCode=""
       },
       //
       addinfosunmit(){
        
       },
       //
       itemschangehandler(){
            for(let item of this.itemchecklist)
            {
                if(this.checkedlists.includes(item.lable))
                {
                    item.show=true
                }
                else{
                    item.show=false;
                }
            }
           
       },
       //
      
    },
    mounted(){
    },created(){
    
    },beforeDestroy(){
    },
}
</script>





