<template>
  <div class="content">
    <h2></h2>
    <div class="table">
      <access_acle v-if="dataready" :option="access"></access_acle>
      <div class="webside">
        <div  class="titletext"></div>
        <div class="prjecttext">:vue2,express,element-ui,canvans,echars</div>
        <div  class="titletext">:</div>
        <div class="prjecttext"><span class="importent">express</span>,</div>
        <div class="prjecttext"><span class="importent">canvas</span>,</div>
        <div class="prjecttext">,<span class="importent">vuex</span></div>
        <div class="prjecttext"></div>
        <div class="prjecttext"><span class="importent">element-ui</span>,<span class="importent">threejs</span>3D,demo</div>
      
      </div>
    </div>
  </div>
</template>
<script>
import access_acle from "../components/access_scale";
import axios from "axios";
export default {
  data() {
    return {
      access: {
        title: {
          text: "",
          subtext: "",
          left: "center"
        },
        tooltip: {
          trigger: "item"
        },
        legend: {
          orient: "vertical",
          left: "left"
        },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: "50%",
            data: [
              { value: null, name: "" },
              { value: null, name: "" },
              { value: null, name: "" }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      },dataready:false
    };
  },
  components: { access_acle },
  methods: {
    addaccessinfo() {
      axios
        .get("/api/addaccessinfo", {
          params: {
            accesstime: new Date().getTime()
          }
        })
        .then(res => {
         
        });
    },
    getaccessinfo() {
      axios.get("/api/getaccessinfo", {}).then(res => {
        this.access.series[0].data.splice(0, 1, {
          value: res.data.total,
          name: ""
        });
        this.access.series[0].data.splice(1, 1, {
          value: res.data.weekaccess,
          name: ""
        });
        this.access.series[0].data.splice(2, 1, {
          value: res.data.toddayaccess,
          name: ""
        });
        this.dataready=true;
      });
    }
  },
  mounted() {
    this.addaccessinfo();
    this.getaccessinfo();
  },
  beforeCreate() {}
};
</script>
<style scoped>
.table {
  display: flex;
  width: 80%;
  margin: auto;
}
.titletext{
  font-size: 1rem;font-weight: 1000;
  margin-bottom:1rem;
}
.prjecttext{

  margin-bottom:1rem;
}
.importent{
  color:red;
}
</style>



